//popup
.pb-popup {
    position: fixed;
    height: 100%;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    z-index: 9999;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    .pb-popup-content {
        top: 125.2px;
        left: 572.1px;
        margin: auto;
        width: 375px;
        background-color: #fff;
        position: absolute;
        -webkit-border-radius: 3px;
        border-radius: 3px;
        -webkit-box-shadow: 2px 8px 23px 3px rgb(0 0 0 / 20%);
        box-shadow: 2px 8px 23px 3px rgb(0 0 0 / 20%);
        overflow: hidden;

        .pb-popup-header {
            text-align: center;
            font-size: 15px;
            color: #495157;
            padding: 30px 0 10px;
            font-weight: 500;
        }

        .pb-popup-message {
            text-align: center;
            padding: 0 30px 30px;
            min-height: 50px;
            font-size: 12px;
            line-height: 1.5;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
        }

        .pb-popup-confirm-buttons-wrapper {
            border-top: 1px solid #e6e9ec;
            text-align: center;
        }

        .pb-popup-buttons {
            font-family: Roboto, Arial, Helvetica, Verdana, sans-serif;
            width: 50%;
            border: none;
            background: none;
            color: #6d7882;
            font-size: 15px;
            cursor: pointer;
            padding: 13px 0;
            outline: 0;
        }

        .pb-popup-buttons:first-child {
            border-right: 1px solid #e6e9ec;
        }

        .pb-popup-confirm-ok {
            color: #b01b1b;
        }
    }
}

